import React , { useState , useEffect } from "react"
import { GET_USER_INFO } from '../queries/index'
import { useQuery , useMutation } from '@apollo/react-hooks';
import Layout from "../components/layout"
import SEO from "../components/seo"
import stl from '../STYLES/USER_INFO/user_info.module.scss'
import StarRatings from 'react-star-ratings';
import { Link } from 'gatsby'
import { ADD_COMMENT , RATE_PERSON} from '../queries/index'
import { ErrorAlert , AlertSuccess } from '../helpers/index'

const roundScore = e => {
const Score = e.reduce((a,b) => a + b , 0) / e.length
return Number(Score.toFixed(1))
}





const UserBody = ({location , getCurrentUser}) => {



const username = location.hash.replace('#/' , '');

const { data } = useQuery(GET_USER_INFO , {
    variables:{username}
});



const [state , setState] = useState(null)
let [comLength , setComLength] = useState(5)
const [comment , setComment] = useState(null)
const [score , setScore] = useState(null)


useEffect(() => {
    data && setState(data.getUserInfo)

}, [data])

const [addComment] = useMutation(ADD_COMMENT , {
  variables:{
    _id:getCurrentUser && getCurrentUser._id,
    username:getCurrentUser && getCurrentUser.username,
    to:state && state.username,
    comment
  },
  onError:(e) => ErrorAlert(e),
  onCompleted:(e) => console.log("aaaaaaa")
})

const [ratePerson] = useMutation(RATE_PERSON , {
  variables:{
    _id:getCurrentUser && getCurrentUser._id,
    to:state && state.username,
    from:getCurrentUser && getCurrentUser.username,
    newScore:score
  },
  refetchQueries:() => [{query:GET_USER_INFO , variables:{username}}],
  onError:(error) => ErrorAlert(error),
  onCompleted:() => AlertSuccess(`Rated ${username} successfully`)
})



const changeRating = (newRating) => {

  if(!getCurrentUser){
   return ErrorAlert("To rate you have to be signed in...")
  
  }
  const PromiseRate = (score) => {
     setScore(score)
     return new Promise((resolve, reject) => {
       resolve(score)
    });
  }

   return PromiseRate(newRating).then(() => ratePerson())

}

const addLength = () => {
  setComLength(comLength += 1)
}


return (
    <div className = {stl.wrapper}>
        {state && 
          <>
           <h1>{state.username}</h1>
           <div className = {stl.infoWrapper}>
           <h3>Characteristics</h3>
           <h4>Name:</h4>
           <span> {state.name ? state.name : "-"}</span>
           <h4>nationality:</h4>
           <span> {state.characteristic.nationality ? state.characteristic.nationality : "-"}</span>
           <h4>languages speak:</h4>
           <span >{state.characteristic.languages.length ? state.characteristic.languages.map(lang => lang + ' ') : '-'}</span>
           <h3>Skills:</h3>
             <span>{state.skills ? state.skills : '-'}</span>
           <h3>About me:</h3>
             <span>{state.aboutMe ? state.aboutMe : '-'}</span>
           <h3>Availability:</h3>
             <div>
               <h4>Hiring employees:</h4> <span> {state.job.hiring ? 'yes' : 'no'}</span>
             </div>
             <div>
                <h4>Hirable: </h4> <span>{state.job.available ? 'yes' : 'no'}</span>
             </div>
             <div>
                <h4>Currently available:</h4> <span> {state.job.available ? 'yes' : 'no'}</span>
             </div>

           <h3> Location</h3>
              <div>
                  <h4>State:</h4> <span>State: {state.location.state ? state.location.state : '-'}</span>
              </div>
              <div>
                  <h4>City:</h4> <span>City: {state.location.city ? state.location.city : '-'}</span>
              </div>
           <h3> Contact info:</h3>
           <div>
           <h4>Email:</h4><span>{state.showEmail ? state.email : 'hidden'}</span>
           <h4>Phone Number:</h4><span>{state.show_Phone_number ? state.phone_number : 'hidden'}</span>
            </div>
           <h3> Rating:</h3>
           <div className = {stl.rating}>
                <StarRatings
                    rating={state.score.length ? roundScore(state.score) : 0}
                    changeRating = {(e) => changeRating(e)}
                    starRatedColor="blue"
                    numberOfStars={5}
                    starHoverColor = 'yellow'
                    name='rating'
                />
           </div>
            <div>
                  <h4>Rating:</h4> <span>{state.score.length && roundScore(state.score)}</span>
           </div>
           <div>
                  <h4>People Rated:</h4> <span>{state.peopleVoted.length}</span>
           </div>
           <h3 style = {{marginBottom:"20px"}}>People's Comments:</h3>
           <ul className = {stl.comments}>
             { state.comments.length 
             ?state.comments.map(({_id , comment , from}) => (
            <li key = {_id}>
              <h4><Link to = {`/user/#/${from.username}`}>{from.username}</Link></h4>
              <span >{comment}</span>
           </li>)).slice(0,comLength)
        : <li><span>No Comments yet</span></li>}

         {comLength < state.comments.length && <button onClick = {() => addLength()}>See more</button>}
        </ul>
        { getCurrentUser && getCurrentUser.username !== state.username && !state.peopleCommented.includes(getCurrentUser._id) &&  <>
        <h3 style = {{marginTop:"20px"}}>Leave a comment:</h3>
        <textarea onChange = {(e) => setComment(e.target.value)} maxLength = '200'>
        </textarea>
        
        <button disabled = {!comment || comment.trim() === ''} onClick = {() => addComment()}>Submit</button>
        
        </>}
        </div>
        </>
        }
      </div>
    )
}




const UserInfo = ({location}) => (
<Layout>
  <SEO title = 'user-info' />
  <UserBody location = {location} />
</Layout>
)

export default UserInfo